import React from 'react'
import Link from 'gatsby-link'
//import GoogleMaps from "../components/map"; 
import GoogleMaps from '../components/maps'
import Punchline from '../components/punchline'
import Disqus from '../components/disqus'//'disqus-react' // react-disqus-thread

class BlogPage extends React.Component {

	render(){
		function handleNewComment(props) {
			console.log('---BlogPage---');
		}
	    return (
	    	<div className="container backpage-main-content">
				<GoogleMaps key="map"/>
				
				<div className="pane">
					<h4 className="pane-title">HEY - Appreciate if you can provide the valuable feedback on portfolio to make it better!</h4>
				</div>
				<Disqus
					shortname="sysforge-disqus-com"
					identifier="sysforge-disqus-com"
					title="SYSFORGE"
					url="https://sysforge-disqus-com.disqus.com"
					category_id="123456"
					onNewComment={this.handleNewComment}/>
				
			</div>
	    )
	}
}

const BlogPagefn = () => (
	<div className="container backpage-main-content">
		<GoogleMaps key="map"/>
		<div className="pane">
			<h4 className="pane-title">HEY - Appreciate if you can provide the valuable feedback on portfolio to make it better!</h4>
		</div>
		<Disqus/>
	</div>
)

export default BlogPagefn



